import axios from 'axios'

export default {
    state: {
        token: localStorage.getItem('access_token') || null,
    },
    getters: {
        loggedIn(state) {
            return state.token !== null
        },
        getToken(state) {
            return state.token
        }
    },
    actions: {
        async REMOVE_TOKEN(context) {
            try {
                console.log('REMOVE_TOKEN')
                delete axios.defaults.headers.common['Authorization']

                localStorage.removeItem('access_token')
                localStorage.removeItem('userFullName')
                localStorage.removeItem('userPhotoUrl')
                context.commit('destroyToken')
            } catch (e) {
                console.log(e)
                context.commit('setError', e)
                throw e
            }
        },
        SET_TOKEN(context, userData) {
            try {
                console.log('SET_TOKEN')
                localStorage.setItem('access_token', userData.hash)
                localStorage.setItem('userFullName', userData.first_name + ' ' + userData.last_name)
                localStorage.setItem('userPhotoUrl', userData.photo_url)
                context.commit('retrieveToken', userData.hash)

                axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
                axios.defaults.headers.post['Authorization'] = 'Bearer ' + context.state.token


            } catch (e) {
                console.log(e)
                context.commit('setError', e)
                throw e
            }
        },
    },
    mutations: {
        retrieveToken(state, token) {
            state.token = token
        },
        destroyToken(state) {
            // console.log('destroyToken(state) {')
            state.token = null
        },
    },
}
