<template>
    <div>
        <v-navigation-drawer v-model="drawer"
                             width="18%"
                             :clipped="$vuetify.breakpoint.lgAndUp"
                             app>
            <left-panel></left-panel>
        </v-navigation-drawer>


        <v-app-bar dark :clipped-left="$vuetify.breakpoint.lgAndUp"
                   app
                   color="cyan darken-4"
                   dense>

            <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>

            <v-toolbar-title style="width: 300px"
                             class="ml-0 pl-4">
                <span class="hidden-sm-and-down">SmartGeo</span>
            </v-toolbar-title>

            <v-text-field dark flat solo-inverted
                          hide-details
                          dense
                          prepend-inner-icon="mdi-magnify"
                          label="Search"
                          class="hidden-sm-and-down">
            </v-text-field>

            <v-spacer></v-spacer>
            <!--
                        <v-btn icon>
                            <v-icon>mdi-apps</v-icon>
                        </v-btn>
                        <v-btn icon>
                            <v-icon>mdi-bell</v-icon>
                        </v-btn>-->
            <v-menu offset-y>
                <template v-slot:activator="{ on }">
                    <v-btn dark icon v-on="on">
                        <v-avatar width="40px" height="40px">
                            <img v-if="userPhotoUrl === 'undefined' || userPhotoUrl === null"
                                 :src="'https://www.w3schools.com/howto/img_avatar.png'" alt="Avatar" class="avatar">
                            <img v-else :src="userPhotoUrl" alt="Avatar" class="avatar">
                        </v-avatar>
                    </v-btn>
                </template>
                <v-list>
                    <v-list-item>
                        <v-list-item-avatar>
                            <img v-if="userPhotoUrl === 'undefined' || userPhotoUrl === null"
                                 :src="'https://www.w3schools.com/howto/img_avatar.png'" alt="Avatar" class="avatar">
                            <img v-else :src="userPhotoUrl" alt="Avatar" class="avatar">
                        </v-list-item-avatar>
                        <v-list-item-content>
                            <v-list-item-title>{{userFullName}}</v-list-item-title>
                            <!--                            <v-list-item-subtitle>Founder of Vuetify.js</v-list-item-subtitle>-->
                        </v-list-item-content>
                    </v-list-item>


                </v-list>
                <v-divider></v-divider>
                <v-list>
                    <v-list-item v-for="(item, i) in header_menu"
                                 :key="i" @click="goto(item.link)">
                        <v-list-item-icon>
                            <v-icon v-text="item.icon"></v-icon>
                        </v-list-item-icon>
                        <v-list-item-title v-text="item.name">
                        </v-list-item-title>
                    </v-list-item>
                </v-list>
            </v-menu>
        </v-app-bar>


        <v-content class="app-content" :class="{full: !drawer}">
            <div class="app-page">
                <router-view/>
            </div>
        </v-content>

       <!-- <v-btn bottom color="pink" dark fab fixed
               right
               @click="show_msg()">
            <v-icon>mdi-plus</v-icon>
        </v-btn>-->
    </div>
</template>

<script>
    // import Navbar from '../components/app/Navbar'
    import LeftPanel from '../components/app/LeftPanel.vue'
    // import Header from '../components/app/Header.vue'
    import messages from "../utils/messages.js";


    export default {
        name: 'main-layout',
        data: () => ({
            drawer: true,
            offset: true,
            date: new Date(),
            interval: null,
            dropdown: null,
            header_menu: [
                {name: 'Профиль', link: '/profile', icon: 'account_circle'},
                {name: 'Выйти', link: '/logout', icon: 'assignment_return'}
            ],
            userFullName: localStorage.getItem('userFullName'),
            userPhotoUrl: localStorage.getItem('userPhotoUrl')
        }),
        methods: {
            async logout() {
                this.$store.dispatch('REMOVE_TOKEN').then(() => {
                    this.$router.push('/login?message=logout')
                });
            },
            goto(path) {
                if (path !== '/logout') {
                    this.$router.push(path)
                } else {
                    this.logout();
                }
            }
        },
        mounted() {
            this.interval = setInterval(() => {
                this.date = new Date()
            }, 1000)

            try {
                // eslint-disable-next-line no-undef
                this.dropdown = M.Dropdown.init(this.$refs.dropdown, {
                    constrainWidth: false
                })
                // eslint-disable-next-line no-empty
            } catch (e) {

            }

        },

        beforeDestroy() {
            // console.log('beforeDestroy')
            clearInterval(this.interval)
            if (this.dropdown && this.dropdown.destroy) {
                this.dropdown.destroy()
            }
        },
        components: {
            LeftPanel
        },
        computed: {
            error() {
                return this.$store.getters.error
            }
        },
        watch: {
            error(error) {
                console.log(error)
                this.$error(messages[error.code] || 'Something went wrong')
                this.$error(error)
            }
        }
    }
</script>
