import Vue from 'vue'
import Vuelidate from "vuelidate";
import App from './App.vue'
import routes from './router/index'
import store from './store'
import dateFilter from './filters/date.filter'
import messagePlugin from './utils/message.plugin.js'
import VueRouter from 'vue-router'
import VueGeolocation from 'vue-browser-geolocation';

import VueToast from 'vue-toast-notification';
import 'vue-toast-notification/dist/theme-default.css';

import axios from 'axios'

import vuetify from './plugins/vuetify';

// axios.defaults.baseURL = 'http://todo-laravel.test/api'
// axios.defaults.baseURL = '//127.0.0.1:8083/api/'
axios.defaults.baseURL = '//192.168.1.13:8779/'

// eslint-disable-next-line no-unused-vars
Vue.use(Vuelidate)
Vue.use(messagePlugin)
Vue.use(VueGeolocation);
Vue.use(VueToast, {
    position: 'top-right'
});
Vue.filter('date', dateFilter)

Vue.use(VueRouter)

const router = new VueRouter({
    routes,
    // mode: 'history'
})

router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (!store.getters.loggedIn) {
            console.log('user NOT logged in')
            next({
                name: 'login',
            })
        } else {
            next()
        }
    } else if (to.matched.some(record => record.meta.requiresVisitor)) {
        if (store.getters.loggedIn) {
            next({
                name: 'home',
            })
        } else {
            next()
        }
    } else {
        next()
    }
})

let app

if (!app) {
    new Vue({
        router,
        store,

        created: function () {
            Vue.prototype.$featureTypeCircle = 'Circle'
            Vue.prototype.$featureTypeLineString = 'LineString'
            Vue.prototype.$featureTypePolygon = 'Polygon'
            Vue.prototype.featureTypePoint = 'Point'
            Vue.prototype.$iconsImgUrl = 'https://icons.uzgps.uz/pins/'
            // if (sessionStorage.redirect) {
            //     const redirect = sessionStorage.redirect
            //     delete sessionStorage.redirect
            //     this.$router.push(redirect)
            // }
        },

        vuetify,
        render: h => h(App)
    }).$mount('#app')
}

Vue.config.productionTip = false

